*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.backgroundImg{
    min-height: 100vh;
    width: 100%;
    background-image: url("./assets/images/background.jpg");
    background-size: contain;
}
.background{
    width: 100%;
    min-height: 100vh;
    background-color: rgba(0,0,0,0.8);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}