.labelWrapper{
    width: 800px;
    height: 500px;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    filter: drop-shadow(1px 1px 15px #ddd);
}
.labelBlock{


    padding: 15px;
}.trackBlock{
     display: flex;
 }
 .trackBlock div{
     display: flex;
     padding: 10px;
 }

.labelBlock input{
    border:none;
    outline: none;
    height: 50px;
    font-size: 24px;
    text-align: center;
    width: 100%;
}
.trackBlock input{
    border:1px solid #6CBB3B;
}
.trackMargin{
    margin-right: 15px;
}
.destination,.trackNumber,.shipper,.code{
    border:2px solid #6CBB3B;
    color: #6CBB3B;
    padding: 5px;
    width: 75%;
    margin-bottom: 10px;
}
.trackNumber,.shipper,.code{
    width: 100%;
}
.logo img{
    width: 140px;
}
.lineBlock,.lineBlockShipper{
    display: flex;
}
.lineBlock .line{
    width: 25%;
}

.lineBlockShipper .line{
    width: 50%;
}

.line{
    display: flex;
    justify-content: space-between;
}

.printButton{
    margin-top: 25px;
    text-align: center;
}
.printButton button{
    padding: 15px 25px;
    background-color: #6CBB3B;
    color:#fff;
    border:none;
    border-radius: 3px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    letter-spacing: 3px;
}
.printButton button:hover{
    background-color: #529529;
}


